var sourceType = [
    ['camera'],
    ['album'],
    ['camera', 'album']
]
var sizeType = [
    ['compressed'],
    ['original'],
    ['compressed', 'original']
]
export default {
	components: {
	},
	data() {
		return {
			contents: '',	// 文本内容
			imageList: [],
			sourceTypeIndex: 2,
			sourceType: ['拍照', '相册', '拍照或相册'],
			sizeTypeIndex: 2,
			sizeType: ['压缩', '原图', '压缩或原图'],
			countIndex: 8,
			count: [1, 2, 3, 4, 5, 6, 7, 8, 9],
			pickIndex: 0,
			pickList: [{name: '不限'}],
			postClassId: '',	// 帖子分类的id， 默认为空
			checked: true,
		}
	},
	// 监听页面卸载， 监听页面的卸载， 当前处于A页面，点击返回按钮时，则将是A页面卸载、
	onUnload() {
	    this.imageList = [],
		this.sourceTypeIndex = 2,
		this.sourceType = ['拍照', '相册', '拍照或相册'],
		this.sizeTypeIndex = 2,
		this.sizeType = ['压缩', '原图', '压缩或原图'],
		this.countIndex = 8;
	},
	onLoad(){
		this.getPostClassList();
		uni.getLocation({
		    type: 'wgs84',
		    success: function (res) {
		        console.log('当前位置的经度：' + res.longitude);
		        console.log('当前位置的纬度：' + res.latitude);
		    }
		});
	},
	// 监听页面的隐藏,当从当前A页跳转到其他页面，那么A页面处于隐藏状态。
	onHide(){
	},
	// 分享
	onShareAppMessage() {
		return {
			title: '邀请您使用小镇事事通',
			path: '/pages/write/write'
		}
	},
	methods: {
		navTo(url){
			uni.navigateTo({ url: url })
		},
		// 获取分类列表
		getPostClassList(){
			let that = this;
			uni.showLoading({
				title: '加载中'
			});
			var query = that.Bmob.Query('postClass');
			// 查询所有数据
			query.find().then(res => {
			  uni.hideLoading();
			  that.pickList = that.pickList.concat(res);
			});
		},
		// 用户发帖--内容和图片, 将该帖子关联该用户
		publish(){
			let that = this;
			if(this.contents.length < 15){
				uni.showToast({
				    title: '内容长度不低于15个字符',
				    icon: 'none'
				});
				return 
			}
			if(this.contents.length > 150){
				uni.showToast({
				    title: '内容长度不得超过150个字符',
				    icon: 'none'
				});
				return 
			}
			that.Bmob.checkMsg(that.contents).then(res => {
				uni.showLoading({
					title: '加载中'
				});
				var currentUser = that.Bmob.User.current(); // 当前用户
				var objectId = currentUser.objectId; // 当前用户Id
				// Pointer 类型在数据库是一个json数据类型，只需调用Pointer方法创建一个Pointer对象存入到字段中，如下：
				const pointer = that.Bmob.Pointer('_User')
				const poiID = pointer.set(objectId)
				var query = that.Bmob.Query('postList');
				
				query.set('author',poiID)
				query.set("contents", that.contents)
				query.set("view", 0)
				query.set("likes", 0)
				query.set("comments", 0)
				query.set("images", that.imageList)
				
				// 关联分类表
				if(that.postClassId){
					const pointerPostClass = that.Bmob.Pointer('postClass')
					const pPostClassID = pointerPostClass.set(that.postClassId)
					query.set('belongsClass',pPostClassID)	// 绑定的帖子分类id
				}
				
				query.save().then(res => {
					uni.hideLoading();
					uni.showModal({
						title: '发表成功',
						content: '是否立即前往查看',
						confirmText: '前往查看',
						cancelText: '继续发帖',
						success: function (res) {
							if (res.confirm) {
								uni.reLaunch({
								    url: '/pages/index/index'
								});
							} else if (res.cancel) {
								that.contents = '';	// 文本内容
								that.imageList = [];
								that.pickIndex = 0;
							}
						}
					});
				})
			}).catch(err => {
				let tips = '错误类型：' + err.code + '，' + err.error;
				if(err.code === 10007){
					tips = '输入内容含有敏感词，请文明用语'
				}
				uni.showToast({
					title: tips,
					icon: 'none'
				})
			})
		},
		sourceTypeChange: function(e) {
		    this.sourceTypeIndex = e.target.value
		},
		sizeTypeChange: function(e) {
		    this.sizeTypeIndex = e.target.value
		},
		countChange: function(e) {
		    this.countIndex = e.target.value;
		},
		chooseImage: async function() {
			let that = this;









		
		    if (this.imageList.length === 9) {
		        let isContinue = await this.isFullImg();
		        console.log("是否继续?", isContinue);
		        if (!isContinue) {
		            return;
		        }
		    }
		    uni.chooseImage({
		        sourceType: sourceType[this.sourceTypeIndex],
		        sizeType: sizeType[this.sizeTypeIndex],
		        count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length :
		            this.count[this.countIndex],
		        success: (res) => {
		   //          this.imageList = this.imageList.concat(res.tempFilePaths);
					// var file;
					// for (let item of res.tempFilePaths) {
					//   console.log('itemn',item)
					//   file = that.Bmob.File('abc.jpg', item);
					// }
					// file.save().then(res => {
					//   console.log(res.length);
					//   console.log(res);
					// })
					var imageSrc = res.tempFilePaths[0]
					uni.uploadFile({
						url: 'http://imlmbm.xyz/weiliao/skill/file/upload',
						filePath: imageSrc,
						fileType: 'image',
						name: 'uploadFile',	// 后台 参数名
						success: (data) => {
							let resp = JSON.parse(data.data)
							// that.userInfo.portrait = that.requestUrl.url + '/' + resp.results;
						},
						fail: (err) => {
							console.log('uploadImage fail', err);
							uni.showModal({
								content: err.errMsg,
								showCancel: false
							});
						}
					});
		        },
		        fail: (err) => {





		        }
		    })
		},
		isFullImg: function() {
		    return new Promise((res) => {
		        uni.showModal({
		            content: "已经有9张图片了,是否清空现有图片？",
		            success: (e) => {
		                if (e.confirm) {
		                    this.imageList = [];
		                    res(true);
		                } else {
		                    res(false)
		                }
		            },
		            fail: () => {
		                res(false)
		            }
		        })
		    })
		},
		previewImage: function(e) {
		    var current = e.target.dataset.src
		    uni.previewImage({
		        current: current,
		        urls: this.imageList
		    })
		},
		async checkPermission(code) {
		    let type = code ? code - 1 : this.sourceTypeIndex;
		    let status = permision.isIOS ? await permision.requestIOS(sourceType[type][0]) :
		        await permision.requestAndroid(type === 0 ? 'android.permission.CAMERA' :
		            'android.permission.READ_EXTERNAL_STORAGE');
		
		    if (status === null || status === 1) {
		        status = 1;
		    } else {
		        uni.showModal({
		            content: "没有开启权限",
		            confirmText: "设置",
		            success: function(res) {
		                if (res.confirm) {
		                    permision.gotoAppSetting();
		                }
		            }
		        })
		    }
		
		    return status;
		},
		// 选择分类提示
		selectTips(){
			uni.showToast({
			    title: '选择分类可以更好地被别人发现，当然，你也可以不选',
				icon: 'none'
			});
		},
		// 切换pick分类选择
		bindPickerChange: function(e) {
			this.pickIndex = e.target.value
			this.postClassId = this.pickList[parseInt(this.pickIndex)].objectId ? this.pickList[parseInt(this.pickIndex)].objectId : ''
		},
	},
	// watch: {
	// 	columnsNum(value) {
	// 	    this.set({
	// 	        displayColumns: this.data.columns.slice(0, +value)
	// 	    });
	// 	}
	// }
}
